import "../App.css";

const PostImage = () => {
  let postNum = 0;
  function handleSubmit(e) {
    e.preventDefault();
    const postTitle = e.target[0].value;
    postNum++;
  }
  <div className="center">
    <form onSubmit={handleSubmit}>
      <label>
        title:
        <input type="text" name="title" />
      </label>
    </form>
  </div>;
};

export default PostImage;
